/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltd-content,
div.woocommerce {

    .eltd-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .eltd-quantity-input,
        .eltd-quantity-minus,
        .eltd-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-heading-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            box-sizing: border-box;
        }

        .eltd-quantity-input {
            position: relative;
            height: 54px;
            width: 54px;
            padding: 0;
            margin: 0 28px 0 0;
            font-family: inherit;
            line-height: 42px;
            outline: 0;
        }

        .eltd-quantity-minus,
        .eltd-quantity-plus {
            position: absolute;
            right: 0;
            width: 28px;
            height: 28px;
            line-height: 27px;
            border-left: 0;
            cursor: pointer;
            @include eltdTransition(color .15s ease-in-out);

            &:hover {
                opacity:0.85;
            }
        }

        .eltd-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .eltd-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .eltd-content,
div.woocommerce {

    input[type="text"],
    input[type="email"]:not(.wpcf7-email),
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 14px;
        padding: 14px 18px;
        font-family: inherit;
        font-size: 14px;
        //line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid $default-border-color;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;

        &:focus {
            border-color: $default-heading-color;
        }
    
        @include eltdPlaceholder {
            color: inherit;
        }
    }

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"] {
        line-height: normal;
        height: 50px;
        padding-top: 0;
        padding-bottom: 0;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */