/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltd-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltd-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 20px;
    }

    .eltd-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .eltd-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-size: 20px;
            line-height: 1em;

            &:before {
                display: block;
                line-height: inherit;
            }

            .eltd-cart-number {
                position: absolute;
                height: 16px;
                width: 16px;
                line-height: 16px;
                text-align: center;
                border-radius: 50%;
                background-color: transparent;
                font-size:14px;
                left:15px;
                top:-10px;
                font-weight: 400;
            }
        }
    }


    &:hover {

        .eltd-shopping-cart-dropdown {
            top: 100%;
            max-height:1500px;
            @include eltdTransition(all 1.25s cubic-bezier(0.46, 0.39, 0.64, 0.47) 0.05s);
        }
    }
}

.eltd-dark-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) .eltd-shopping-cart-holder .eltd-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }

    .eltd-cart-icon {

        .eltd-cart-number{

            color:$default-heading-color !important;

        }
    }
}

.eltd-light-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) .eltd-shopping-cart-holder .eltd-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }

    .eltd-cart-icon {

        .eltd-cart-number{

            color:$default-light-color;

        }
    }
}

.eltd-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 290px;
    background-color: #fff;
    box-shadow:inset 0 0 0 1px $default-border-color;
    text-align: left;
    z-index: 1000;
    max-height:0;
    overflow:hidden;
    box-sizing: border-box;
    @include eltdTransition(all .75s cubic-bezier(0.46, 0.39, 0.64, 0.47) 0.05s);

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include eltdTableLayout();
            padding: 5px 0;
            box-sizing: border-box;

            &.eltd-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:nth-last-child(2) {
                border-bottom:1px solid #666666;
                padding-bottom: 19px;
            }
        }
    }

    .eltd-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltd-item-info-holder {
        @include eltdTableCellLayout();
        vertical-align: top;
        padding: 17px 0 17px 10px;

        h5.eltd-product-title{
            margin: 0;
        }
        .eltd-product-title a{
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            font-size: 13px;
            text-transform: none;
        }


        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 18px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltd-quantity {
            display: inline;
            margin: 3px 0 0;
            font-size: 13px;
            color:$default-info-color;

        }

        .amount {
            display: inline;
            margin: 3px 0 0;
            color: $default-info-color;
            font-size: 13px;
        }
    }

    .eltd-cart-bottom {
        padding: 10px 0 9px;

        .eltd-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 16px;
            font-weight: 600;

            .eltd-total{
                font-size: 11px;
            }

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
                font-weight: 500;
                letter-spacing: 2px;

            }

            .eltd-total-amount {
                text-align: right;
	            font-size: 13px;
                font-weight: 500;
            }
        }

        .eltd-view-cart {
            @include eltdRelativeHolderLayout();
            height: 44px;
            line-height: 44px;
            padding: 0 34px;
            font-size: 11px;
            background: $default-heading-color;
            font-weight: 500;
	        letter-spacing: 2px;
            color: #fff;
            text-align: center;
            box-sizing: border-box;
            @include eltdTransition(color .2s ease-out, background-color .2s ease-out);
            border-top:1px solid $default-heading-color;
            border-bottom:1px solid $default-heading-color;

            span {
                display: block;
            }

            &:hover {
                color: $default-heading-color;
                background-color: transparent;
                border-top:1px solid $default-heading-color;
                border-bottom:1px solid $default-heading-color;
                border-left:none;
                border-right:none;
            }
        }
    }
}

.eltd-vertical-menu-area {

    .eltd-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */