/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    font-family: $heading-text-font;
    top: 35px;
    width: auto;
    height: 50px;
    margin: 0;
    padding: 0 19px;
    background-color: #fff;
    color:$default-menu-color;
    font-size: 21px;
    line-height: 50px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdTransform(translateZ(0));
}

@mixin eltdWooCommerceSaleStyle() {
    left: 0;
}

@mixin eltdWooCommerceOutOfStockyStyle() {
    right: 0;
}
/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-text-color;

    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 14px;
    line-height: 1.2em;
    letter-spacing: 6px;
    color: #989898;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
        color: $default-heading-color;
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            color: $default-heading-color;
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin eltdWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */