/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltd-woo-single-page {
	
	&.eltd-woo-single-thumb-on-left-side {
		
		.eltd-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .eltd-single-product-content {
        @include eltdRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 19px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltd-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 5px;
        float: left;
        box-sizing: border-box;
	
	    .eltd-single-product-title {
		    display: block;
		    margin: -14px 0 14px;
	    }
	
	    .price {
		    @include eltdWooCommercePriceStyle();
			font-size: 18px;
	    }
	
	    .woocommerce-product-rating {
		    @include eltdRelativeHolderLayout();
		    line-height: 1;
		    margin: 21px 0 3px;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdRelativeHolderLayout();
		    margin: 36px 0 0;
			line-height: 26px;

		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 39px 0 0;
		
		    > span {
			    display: block;
			    margin: 0;
				font-weight: 400;
				color: $default-heading-color;
					> span {
						font-weight: 300;
					}


			    a {
				    color: $default-info-color;
					font-weight: 300;
				
				    &:hover {
					    color: $default-menu-color;
				    }
			    }
		    }
	    }
	
	    .eltd-woo-social-share-holder {
		    @include eltdRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
				font-weight: 700;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
			form.cart {
		    @include eltdRelativeHolderLayout();
		    margin: 43px 0 0;
	    }
	
	    .eltd-quantity-buttons {
		    margin: 0 25px 0 0;

	    }
    }
	
	.woocommerce-tabs {
		@include eltdRelativeHolderLayout();
		margin-top:50px;
		p{
			line-height: 26px;
		}
		.ui-accordion-header,.ui-widget-content{
			border-color: transparent;
		}

		.eltd-title-tab-holder.ui-accordion-header {
			border-top: 1px solid #ebebeb;
			border-bottom: 1px solid #ebebeb;
			padding: 20px 0;
			margin: 0 0 -1px 0 ;
		}

	.mkd-accordion-mark{
		webkit-transition: color .2s ease-out, background-color .2s ease-out;
		-moz-transition: color .2s ease-out, background-color .2s ease-out;
		transition: color .2s ease-out, background-color .2s ease-out;
	}

		.eltd_icon_plus{
			position: absolute;
			margin-top: 3px;
            @include eltdTransition(opacity .2s ease-out);
		}
		.eltd_icon_minus{
			opacity: 0;
            @include eltdTransition(opacity .2s ease-out);
		}

		.ui-state-active{
			.eltd_icon_minus{
				opacity: 1;
			}
			.eltd_icon_plus{
				opacity: 0;
			}
		}


		ul.tabs {
			@include eltdRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px;
				}
			}
		}
		
		.entry-content {
			@include eltdRelativeHolderLayout();
			box-sizing: border-box;
			padding: 35px 0 35px 27px;
			overflow: hidden;

			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-size: 15px;
				text-transform: capitalize;
				font-weight: 400;
				line-height: 1.83em;
			}

			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}

					.woocommerce-review__author {
						font-weight: 400;
						font-size: 15px;
						text-transform: capitalize;
						color:$default-heading-color;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
					font-size: 17px;
				}
				.comment-form-rating{
					margin-top: 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include eltdWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 24px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 42px;
			text-align: left;
			font-size: 35px;
		}
		
		.product {
			
			.eltd-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */