/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.eltd-woo-pagination-holder {
    @include eltdRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .eltd-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: center;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                margin: 0 8px;
                padding: 0;
                vertical-align: top;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: $default-text-color;
                @include eltdTransition(color .2s ease-out);
                font-size: 15px;
                font-weight: 300;

                &:hover,
                &.current {
                    color: $default-heading-color;
                }
            }

            a {

                &.next,
                &.prev {
                    position: absolute;
                    font-size: 0;
                    line-height: 1;
                    height: 26px;
                    width: 10px;
                    margin: 3px 6px 0 6px;

                    &:before,
                    &:after {
                        position: relative;
                        content: "";
                        display: block;
                        height: 10px;
                        width: 1px;
                        background-color: $default-heading-color;
                        @include eltdTransition(background-color 0.15s ease-out, transform 0.15s ease-out);

                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }

                    &:before {
                        bottom: -2px;
                    }

                    &:after {
                        top: -2px;
                    }
                }

                &.prev {
                    left: 0;
                }

                &.next {
                    right: 0;
                }

                &.prev:hover {
                    color: $default-heading-color;

                    &:before {
                        @include eltdTransform(rotate(45deg));
                    }
                    &:after {
                        @include eltdTransform(rotate(-45deg));
                    }
                }

                &.next:hover {
                    color: $default-heading-color;

                    &:before {
                        @include eltdTransform(rotate(-45deg));
                    }
                    &:after {
                        @include eltdTransform(rotate(45deg));
                    }
                }
            }
        }
    }
}

.eltd-woo-view-all-pagination {
    display: none;
    position: relative;
    //display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $default-text-color;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */