/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .eltd-content,
div.woocommerce {

    input[type="submit"],
    button[type="submit"]:not(.eltd-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button),
    .checkout-button {
        @include eltdButtonDefaultStyle();
        @include eltdButtonMediumParams();
        @include eltdButtonSolidColor();
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;

        &:after {
            display:none;
        }

        &:hover {
            color: #212121;
            background-color: transparent;
            border-top: 1px solid #212121;
            border-bottom: 1px solid #212121;
            border-left: 1px solid transparent;
            border-right: 1px solid transparent;
        }
    }


    a.button:not(.checkout-button),
    a.added_to_cart:not(.checkout-button ){
        font-family: inherit;
        position: relative;
        display: inline-block;
        width: auto;
        outline: none;
        line-height: 2em;
        letter-spacing: 2px;
        text-transform: uppercase;
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        color:#fff;
        letter-spacing: 2.5px;
        background-color: transparent;
        border: 0;
        font-size: 12px;
        font-weight: 400;
        vertical-align: middle;


        &:hover {
            &:after {
                width: 100%;
            }
        }

        &:after{
            position:absolute;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            content: "";
            height: 1px;
            width: 30%;
            display: block;
            background-color: #fff;
            @include eltdTransition(width 0.15s ease-out);
        }

    }

    a.button.added {
        display: none;
    }

    button.eltd-woo-search-widget-button {
        @include eltdTransform(rotate(270deg));
        padding: 0;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */